@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;700;800;900&display=swap');
* {
  margin: 0 auto;
  font-family: 'Nunito', sans-serif;
  outline: none;
  border: none;
}

a{
  color: white;
}

.header-home {
  color: white;
  font-size: 36px;
  max-width: 550px;
  text-align: left;
}
.header-home::selection{
  background-color: #FFC0CB;
}
.header-home::-moz-selection{
  background-color: #FFC0CB;
}
.home-main{
  display: flex;
  flex-direction: row;
  width: 80%;
}

body {
  background: #0F192E;
}

.animations {
  display: flex;
}

#nav-bar {
  height: 100px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  /* justify-content: space-between; */
}
#nav-bar > * {
  width: calc(100% / 3);
}
.logo-link{
  display: flex;
}


.menu-items {
  color: white;
  font-size: 20px;
  text-decoration: none;
  opacity: 80%;
}



.menu-items:hover {
  color: hsl(36, 87%, 79%);
}

#wallet {
  max-width: 50%;
  margin-top: 48px;
}

.main {
  display: flex;
}

#invest {
  margin-top: 100px;
  margin-right: 48px;
  max-width: 70%;
}

#playNow {
  margin-top: 40px;
  width: 182px;
  height: 56px;
  background: #F8D299;
  border: 3px solid rgba(115, 72, 8, 0.2);
  box-sizing: border-box;
  border-radius: 12px;
  color: #734808;
  font-size: 20px;
  cursor: pointer;
}

#playNow:hover {
  border: none;
  background: white;
  color: #0F192E;
}

.box-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.box-container-second {
  display: flex;
  flex-direction: row;
  margin-top: 48px;
  justify-content: space-between;
  align-items: center;
}

.box {
  width: 500px;
  height: 400px;
  background: linear-gradient(224.74deg, #566CDE 14.85%, #A47DE3 44.34%, #D484E1 63.4%, #E68282 101.96%);
  border-radius: 16px;
  outline: none;
}

.box-signup {
  width: 500px;
  height: 450px;
  background: linear-gradient(224.74deg, #566CDE 14.85%, #A47DE3 44.34%, #D484E1 63.4%, #E68282 101.96%);
  border-radius: 16px;
  outline: none;
}

.login-header {
  color: white;
  font-size: 36px;
  margin-bottom: 36px;
  text-align: center;
}

.login-input {
  width: 444px;
  height: 56px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  font-size: 18px;
  color: rgba(255, 255, 255, 0.5);
  border: none;
  margin-top: 48px;
}

.login-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
  /* padding-left: 12px; */
}

.login-btn {
  width: 200px;
  height: 56px;
  background: #F8D299;
  border: none;
  box-sizing: border-box;
  border-radius: 16px;
  font-size: 20px;
  color: #734808;
  cursor: pointer;
}

.signup-btn {
  width: 150px;
  height: 56px;
  background: #F8D299;
  border: none;
  box-sizing: border-box;
  border-radius: 16px;
  font-size: 20px;
  color: #734808;
  cursor: pointer;
}

.login-btn:hover {
  color: #0F192E;
  background: white;
}

.signup-btn:hover {
  color: #0F192E;
  background: white;
}

input[type=text] {
  padding-left: 16px;
}

input[type=password] {
  padding-left: 16px;
}

input[type=email] {
  padding-left: 16px;
}
input[type=number] {
  padding-left: 16px;
}

.hm {
  color: #0F192E;
}

.linksi {
  color: lavender;
  padding-left: 6px;

}

.linksi:hover {
  color: white;
}

.login-msg {
  color: white;
  text-align: center;
  margin-top: 24px;
}

.box-signup input[type="text"],
.box-signup input[type="email"],
.box-signup input[type='password'] {
  width: 444px;
  height: 56px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  font-size: 18px;
  color: rgba(255, 255, 255, 0.5);
  border: none;
  margin-top: 30px;
  margin-left: 28px;
}

.box-signup input[type="text"]::placeholder,
.box-signup input[type="email"]::placeholder,
.box-signup input[type='password']::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.box-container-second-signup {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 25px;
}


.bottom-left-home{
  margin-left: 55px;
}

#id_myself{
  display: none;
}

.account-number{
  margin-top: 24px;
}
.nav-heading {
  color: white;
  margin-left: auto;
  text-align: center;
}
.logo{
  align-self: center;
}
.form-parent{
  margin-top: 3vh;
  max-width: 500px;
  height: 400px;
  border-radius: 16px;
}
.signup-parent{
  display: flex;
  flex-direction: column;
  margin-top: 3vh;
