  font-size: 16px;
}
#contact-message{
  width: 420px;
  height: 150px;
  resize: none;
  border-radius: 5px;
  margin-top: 2vh;
  font-size: 16px;
  
}
#contact-email{
  width: 420px;
  height: 45px;
  border-radius: 5px;
  margin-top: 2vh;
  font-size: 16px;
}
#contact-subject{
  width: 420px;
  height: 45px;
  border-radius: 5px;
  margin-top: 2vh;
  font-size: 16px;
}
#contact-name{
  width: 420px;
  height: 45px;
  border-radius: 5px;
  margin-top: 2vh;
  font-size: 16px;
}

#contact-button, .signup-button {
  margin-top: 2vh;
  height: 50px;
  width: 120px;
  color: white;
  background: transparent;
