}
.contact-flex{
  display: flex;
  width: fit-content;
  align-items: center;
  margin-top: 5vh;
}

.nitro-details{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.nitro-text{
  color: #0F192E;
  text-align: left;
  margin-left: 15px;
}
.nitro-div{
  max-width: 500px;
  background-color: #00edea;
  height: 170px;
  border-radius: 16px;
  display: flex;
  align-items: center;
}
.playnow-home > div{
