}
.verify-text{
  color: white;
  font-family: inherit;
  text-align: center;
  font-weight: 700;
  font-size: 24px;
  max-width: 500px;
}
.success-parent{
  display: flex;
  flex-direction: column;
}
.redirect-dashboard{
  margin-top: 5vh;
  width: 250px;
  height: 50px;
  background: #405CEE;
  color: white;
  font-size: 18px;
  font-weight: 700;
  border-radius: 16px;
  cursor: pointer;
  transition: .3s all;
}
.redirect-dashboard:hover{
  opacity: 0.8;
}
.loginForm-parent{
  margin-top: 3vw;
  max-width: 600px;
  height: 450px;
  background: white;
  border-radius: 32px;
  display: flex;
}
.loginForm-child{
  width: 60%;
}
.loginImage{
  width: 40%;
  max-height: 100%;
  margin: 0;
  border-top-left-radius: 32px;
  border-bottom-left-radius: 32px;
}
.loginForm{
  display: flex !important;
  flex-direction: column !important;
  align-items: center !important;
}
.profile-parent{
  display: flex;
  align-items: center;
}
.profile-box{
  width: 300px;
  height: 380px;
  display: flex;
  flex-direction: column;
  background: lavenderblush;
  border-radius: 32px;
  margin-top: 5vh;
}
.profile-picture{
  border-radius: 100%;
  max-width: 60%;
  max-height: 60%;
}
.profile-gradient{
  display: flex;
  background: #EC953F;
  background: -webkit-linear-gradient(top left, #EC953F, #B104D2);
  background: -moz-linear-gradient(top left, #EC953F, #B104D2);
  background: linear-gradient(to bottom right, #EC953F, #B104D2);
  width: 100%;
  height: 150px;
  border-top-left-radius: 32px;
  border-top-right-radius: 32px;
  align-items: center;
}
.profile-details{
  margin-top: 3vh;
}
.profile-username{
  text-align: center;
  margin-top: 3vh;
}
.rank-flex{
  display: flex;
  max-width: 500px;
  /* height: 700px;
  max-height: 1000px; */
  background: white;
  border-radius: 32px;
}
.rank-number{
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
}
.leaders-name{
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
}
.leaders-img{
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 0px;
  margin-left: 20px
}
.leaders-balance{
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
}
.rank-picture{
  width: 60%;
  border-radius: 20%;
  margin: 10px;
}
.profile-link{
  text-decoration: none;
  color: black;
  font-size: 18px;
}
.sidebar {
  height: 100%; /* 100% Full-height */
  width: 0; /* 0 width - change this with JavaScript */
  position: fixed; /* Stay in place */
  z-index: 1; /* Stay on top */
  top: 0;
  left: 0;
  background-color: #111; /* Black*/
  overflow-x: hidden; /* Disable horizontal scroll */
  padding-top: 60px; /* Place content 60px from the top */
  transition: 0.5s; /* 0.5 second transition effect to slide in the sidebar */
}

/* The sidebar links */
.sidebar a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

/* When you mouse over the navigation links, change their color */
.sidebar a:hover {
  color: #f1f1f1;
}

/* Position and style the close button (top right corner) */
.sidebar .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}
.sidebar-links{
  margin-top: 10vw;
}
.signup-flex{
  display: flex;
  align-items: center;
  width: fit-content;
  margin-top: 5vh;
}
.signup-form{
  display: flex;
  flex-direction: column;
  /* align-items: center; */
}
.signup-email, .signup-username, .signup-password{
  width: 420px;
  height: 45px;
  border-radius: 5px;
  margin-top: 2vh;
  font-size: 16px;
}
.form-padding{
  margin-left: 100px;
}
.user-notfound{
  color: white;
}
.signup-link:link, .signup-link:visited {
  color: white !important;
}

.signup-para{
  color: white;
  font-size: 18px;
  margin-top: 2vh;
  text-align: center;
}

/*    How to Play    */

.tutorial-items {
  width: 75%;
}
.tutorial-items h1 {
  color: #ffffff;
  font-size: 40px;
  margin: 60px 0;
}
.tutorial-items p {
  color: #ffffff;
  font-size: 18px;
}
.tutorial-items img {
  max-height: 24rem;
  max-width: 100%;
  animation: floating 4s ease-in-out infinite;
  filter: drop-shadow(0px 0px 8px rgb(0, 245, 253));
  border-radius: 20px;
}
.tutorial-items .section {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: space-evenly;
  flex-direction: row;
  padding: 60px 0;
}
.tutorial-items .section:nth-child(2n) {
  flex-direction: row-reverse;
}
.tutorial-flex-item {
  width: 40vw;
  padding: 40px;
}
@keyframes floating { 
  0% { transform: translate(0,  -4px); } 
  50%  { transform: translate(0, 4px); } 
  100%   { transform: translate(0, -4px); }     
} 

/*    End of How to Play    */

.gravatar-text{
  color: white;
  font-size: 18px;
  text-align: center;
  margin-top: 5vh;
}


/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
  .sidebar {padding-top: 15px;}
  .sidebar a {font-size: 18px;}
}
@media screen and (max-width: 500px) {
    .nav-heading{
      font-size: 0px !important;
    }
}
@media screen and (max-width: 1200px) {
  
  .contact-flex, .signup-flex{
    flex-direction: column;
  }
  .home-main{
    flex-direction: column;
  }
  .playnow-home{
    display: flex;
    flex-direction: column;
  }
  .playnow-home > div{
    margin: auto !important;
  }
  .signup-flex > div {
    width: 70% !important;
  }
  .form-padding{
    margin-left: 0;
  }
  .header-home{
    font-size: 36px;
    text-align: center;
  }
  .nitro-text{
    font-size: 24px;
  }
  .nav-heading{
    font-size: 18px;
  }
  .adjustLottie > div {
    width: 70% !important;
  }
  .verify-text{
    font-size: 18px;
  }


  .tutorial-items {
    width: 98%
  }
  .tutorial-items h1 {
    font-size: 55px;
    text-align: center;
  }
  .tutorial-items p {
    font-size: 20px;
    text-align: center;
    margin: 10px;
  }
  .tutorial-items img {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  .tutorial-items .section {
    flex-wrap: wrap;
  }
  .tutorial-flex-item {
    width: 90%;
  }
}

